@import url(https://fonts.googleapis.com/css?family=Nunito:100,200,300,400,500,600);
.header-container {
    padding: 20px 10px;
    margin-bottom: 20px;
    border-radius: 5px;
}

.header-text {
    font-size: 28px;
    color: #54d192;
}
.auth-header {
  margin-bottom: 20px;
}

.auth-form button[disabled] {
  background: #54d192 !important;
}

.auth-form button:hover {
  background: gray;
}

.auth-info {
  font-size: 14px;
  margin-top: 15px;
}

@media only screen and (min-width: 500px) {
  .auth-container {
    width: 420px;
  }
}

section.buttons {
    padding: 20px 10px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  color: #54d192;
  -webkit-transition: .3s all;
  transition: .3s all;
}

a:hover {
  color: gray;
}

.main-container {
  min-height: 100vh;
  background: #fcfcfc;
  display: flex;
  align-items: center;
  font-family: "Nunito", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  padding: 40px 0px;
}

.sub-container {
  display: block;
  margin: 0 auto;
}

.content-container {
  background: white;
  padding: 30px 20px;
  border-radius: 5px;
}

.heading {
  font-size: 27px;
  text-align: center;
}

.submit-btn {
  background: #54d192;
  color: white;
  border: none;
  -webkit-transition: .3s all;
  transition: .3s all;
  cursor: pointer;
}
